.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition:
    opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;
  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;

    background-color: $color-deep-sky-blue;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;

    &:hover {
      background-color: rgba(4, 20, 39, 0.25);
    }

    &:focus {
      background-color: rgba(4, 20, 39, 0.25);
      outline: 1px solid $color-semi-transperent;
    }

    &:active {
      background-color: rgba(4, 20, 39, 0.3);
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.modal h2 {
  margin-bottom: 15px;
}

.modal p {
  margin-bottom: 40px;
  color: $color-basic-white;
}

.modal__form {
  display: flex;
  flex-direction: column;
}

.modal__form label {
  color: $color-basic-white;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 25px;
  }
}

.modal__form input {
  width: 100%;
  padding: 15px;
  color: $color-basic-white;
  background-color: $background-form-transparent;
  border: none;

  transition: background-color 300ms ease;

  &:hover {
    background-color: rgba(4, 20, 39, 0.25);
  }

  &:focus {
    background-color: rgba(4, 20, 39, 0.25);
    outline: 1px solid $color-semi-transperent;
  }

  &:active {
    background-color: rgba(4, 20, 39, 0.3);
  }
}

.modal__form textarea {
  min-height: 155px;
  height: 100%;
  width: 100%;
  padding: 25px 15px;
  border: none;
  color: $color-basic-white;
  background-color: $background-form-transparent;
  transition: background-color 300ms ease;

  &:hover {
    background-color: rgba(4, 20, 39, 0.25);
  }

  &:focus {
    background-color: rgba(4, 20, 39, 0.25);
    outline: 1px solid $color-semi-transperent;
  }

  &:active {
    background-color: rgba(4, 20, 39, 0.3);
  }
}

.modal__form button {
  margin-bottom: 20px;
}
