  src: url("../fonts/pt-mono_regular.woff2") format("woff2");
}
@font-face {
  font-style: bold;
  font-weight: 700;
  font-family: "PTmono";
  font-display: swap;
  src: url("../fonts/pt-mono_regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-regular.woff2") format("woff2");
}
@font-face {
  font-style: bold;
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-regular.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "PTmono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: rgb(242, 245, 246);
}

p {
  margin: 0;
  padding: 0;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px rgb(242, 245, 246);
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn {
  display: block;
  padding: 10px 20px;
  color: rgb(242, 245, 246);
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: bold;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  font-size: 14px;
  line-height: 16px;
  background-color: #00b2ff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--black {
  background-color: #041827;
  color: rgb(242, 245, 246);
  border: none;
}
.btn--menu-black {
  background-color: #041827;
  color: rgb(242, 245, 246);
  border: 1px solid rgb(242, 245, 246);
}
.btn--white {
  background-color: rgb(242, 245, 246);
  color: #00b2ff;
  border: #00b2ff 1px solid;
}
.btn--intro {
  padding: 15px 25px;
  font-size: 18px;
  line-height: 21px;
}
.btn--form {
  padding: 15px 25px;
  font-size: 18px;
  line-height: 21px;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn:active {
  opacity: 0.6;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn:hover {
  background-color: rgba(4, 20, 39, 0.25);
}
.modal__close-btn:focus {
  background-color: rgba(4, 20, 39, 0.25);
  outline: 1px solid rgba(248, 253, 255, 0.7);
}
.modal__close-btn:active {
  background-color: rgba(4, 20, 39, 0.3);
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal h2 {
  margin-bottom: 15px;
}

.modal p {
  margin-bottom: 40px;
  color: #ffffff;
}

.modal__form {
  display: flex;
  flex-direction: column;
}

.modal__form label {
  color: #ffffff;
  margin-bottom: 20px;
}
.modal__form label:last-child {
  margin-bottom: 25px;
}

.modal__form input {
  width: 100%;
  padding: 15px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  transition: background-color 300ms ease;
}
.modal__form input:hover {
  background-color: rgba(4, 20, 39, 0.25);
}
.modal__form input:focus {
  background-color: rgba(4, 20, 39, 0.25);
  outline: 1px solid rgba(248, 253, 255, 0.7);
}
.modal__form input:active {
  background-color: rgba(4, 20, 39, 0.3);
}

.modal__form textarea {
  min-height: 155px;
  height: 100%;
  width: 100%;
  padding: 25px 15px;
  border: none;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  transition: background-color 300ms ease;
}
.modal__form textarea:hover {
  background-color: rgba(4, 20, 39, 0.25);
}
.modal__form textarea:focus {
  background-color: rgba(4, 20, 39, 0.25);
  outline: 1px solid rgba(248, 253, 255, 0.7);
}
.modal__form textarea:active {
  background-color: rgba(4, 20, 39, 0.3);
}

.modal__form button {
  margin-bottom: 20px;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
